@import "../utils/_variables";
@import "../utils/_mixins";
@import "../../libs/include-media/dist/_include-media";

/* options panel */
.p-options {
	position: relative;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	@include media('<tab-lg') {
		border: 1px solid $border-color;
		padding: round-float($grid-space*0.8, 1) $grid-space; }

	&:last-child {
		margin-bottom: 0; } }

.p-options__list {
	display: flex;
	justify-content: space-between;
	//justify-content: stretch
	align-items: center;
	//flex-wrap: wrap

	.no-flexbox & {
		display: table;
		width: 100%;
		table-layout: fixed; }

	@include media('<tab-sm') {
		&, .no-flexbox & {
			display: block; } } }

.p-options__item {
	@include font-d;
	text-align: center;
	flex: 0 0 auto;

	@include media('<tab-lg') {
		//flex: 1 1 100px

		&:nth-last-child(2) {
			text-align: right;
 } }			//flex: 1 1 100px

	@include media('<tab-sm') {
		margin-bottom: $bottom-space--sm;
		text-align: center;

		.no-flexbox & {
			display: block; } }

	&:first-child {
		text-align: left;
		//flex: 1 1 100px

		@include media('<tab-sm') {
			text-align: center; } }

	&:last-child {
		text-align: right;
		//flex: 1 1 100px

		@include media('<tab-sm') {
			text-align: center; } }

	.no-flexbox & {
		display: table-cell;
		vertical-align: middle; }

	.view-switcher {
		@include display-inline-block-top; }

	&.p-options__item--filters-show,
	&.p-options__item--view-switcher {
		flex: 1 1 0; } }

.btn-filters-show {
	& > span {
		@include hide-text; } }

//.view-switcher-wrap
//include media('<tab-lg')
//display: none

.p-options__inner {
	position: relative; }

.p-options__group {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	user-select: none;
	text-align: left;

	//+media('<tab-lg')
	//ustify-content: flex-end

	//+media('<tab-sm')
	//ustify-content: center

	.p-options__label {
		@include display-inline-block-middle;
		margin: 4px 25px 4px 0;
		@include font(inherit, inherit);
		color: $text-color-light;

		@include media('<desk-md') {
			margin-right: 15px; } } }

.p-sorting {
	@include display-inline-block-middle;

	&, .#{$user-content} & {
		margin: 0;

		li {
			padding: 0;
			margin: 4px 18px 4px 0;

			@include media('<desk-md') {
				margin-right: 10px; }

			&::before {
				content: none; }

			//&.active
 } }			//adding-right: 38px

	li {
		@include display-inline-block-top;
		white-space: nowrap;
		position: relative;
		cursor: pointer;

		// стрелочка развернута ВВЕРХ, сортировака ПО ВОЗРАСТАНИЮ
		.arr {
			position: absolute;
			top: 50%;
			right: 0;
			@include triangle(top, #{$text-color}, 4px, 2);
			margin-top: -4px;
			opacity: 0;
			transform: rotate(0) translateZ(0);
			transition: {
				property: transform, opacity;
				duration: $animation-speed; }

			.order-desc & {
				transform: rotate(0.5turn) translateZ(0); } } }

	a {
		display: block;
		position: relative;
		text-decoration: none;
		color: inherit;
		padding: 0 12px 0 0;

		// hover
		&:hover {
			color: $base-color; } }

	// активное соотояние
	li.active {

		a {
			color: $base-color;

			& > span {
				border-bottom-color: transparent; }

			// стрелочка развернута ВНИЗ, сортировака ПО УБЫВАНИЮ
			.arr {
				opacity: 1;
				border-bottom-color: $base-color; } } } }

/* options panel end */
